    padding-left: 15px;
    font-size: 20px;
    float: left;
    background-position: 98% 15px;
    background-size: 23px;;
    background-color:#fff;
}

.header_button{
    display: block;
    width: 50px;
    height: 50px;
    background:#fff url(data:image/png;base64,) no-repeat;
    background-size: 25px;
    background-position: center;
    
    border: none;
    outline: none;
    cursor: pointer;
    float: left;
    position: static;

}

.schoolinfo_result{
    width: 200px;
    height: 200px;
    background-color: green;
}


.school_result_view>ul>li
{
    float: left;
