}

 
.emial_header_button {
    background-size: 25px;
    display: block;
    width: 140px;
    height: 50px;
    position: static;
    font-family: 'Roboto', sans-serif;
    font-size: 15px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    font-weight: 500;
    color: #fff;
    background: linear-gradient(45deg, #FE6B8B 30%,#f8819a 90%);
    border: none;
    border-radius: 45px;
    transition: all 0.3s ease 0s;
    cursor: pointer;
    outline: none;
    background-position: center;
    float: left;

    }
  
  .emial_header_button:hover {
    background-color:#000;
    box-shadow: 0px 15px 20px;
    transform: translateY(-3px);
  }

/*이메일 부분 끝 */




.check{
    height: 50px;
    width: 100%;
    display: block;
    font-size: 20px;
    margin-top: 50px;

}
 


.check_button {
    margin-right: 20px;
	box-shadow:inset 0px 39px 0px -24px #ffffff;
	background-color:#ffffff;
	border-top-left-radius:4px;
	border-top-right-radius:4px;
	border-bottom-right-radius:4px;
	border-bottom-left-radius:4px;
text-indent:0;
	border:0.989583px solid #fe6b8b;
	display:inline-block;
	color:#fe6b8b;
	font-family:arial;
	font-size:15px;
	font-weight:bold;
	font-style:normal;
height:50px;
	line-height:50px;
width:80px;
	text-decoration:none;
	text-align:center;
	text-shadow:0px 1px 0px #fe6b8b;
}.check_button:hover {
	background-color:#ffffff;
}.check_button:active {
	position:relative;
	top:1px;
}

